import React, { useEffect, useState } from 'react'
import './css/style.css';

const Weather = () => {

    const [city, setCity] = useState(null);
    const [search, setSearch] = useState('Pune');

    useEffect( () => {
        const fetchApi = async () => {
            const url = `http://api.openweathermap.org/data/2.5/weather?q=${search}&units=metric&appid=6620199c1466e9326f4d6cbbb06175f7`;
            const response = await fetch(url);
            const result = await response.json();
            //console.log(result);
            setCity(result.main);
        }
        fetchApi();
    }, [search])

    const inputElement = (event) =>{
        setSearch(event.target.value);
    }

    return (
        <>
           <div className="box">
                <div className="inputData">
                    <input type="search" value={search} className="inputField" onChange={inputElement} />
                </div>
            {!city ? (
                <p className="errorMsg">No data found</p>
            ) : (
                <>
                    <div className="info">
                        <h2 className="location">
                        <i className="fas fa-street-view">{search}</i>
                        </h2>
                        <h1 className="temp">
                            {city.temp}°Cel
                        </h1>
                        <h3 className="tempmin_max">Min : {city.temp_min}°Cel | Max : {city.temp_max}°Cel</h3>
                    </div>
                    <div className="wave -one"></div>
                    <div className="wave -two"></div>
                    <div className="wave -three"></div>
                </>
            )}
                
            </div>
        </>
    )
}

export default Weather;